footer {
  width: 100%;
  position: fixed;
  bottom: 0;
  display: block;
  justify-content: center;
  align-items: center;
  border-top: 4px solid rgb(255, 255, 255);
  background: rgb(9, 86, 88);
}

footer .social-media-icons {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

footer .social-media-icons li,
footer a {
  list-style: none;
}
footer .social-media-icons li a {
  font-size: 2em;
  color: white;
  margin: 0 10px;
  display: inline-block;
  transition: 0.5s;
}

footer .social-media-icons li a:hover,
footer a:hover {
  color: #ff0000;
  transform: scale(1.3);
}

footer a {
  text-decoration: none;
  color: white;
  text-align: center;
  display: block;
  width: 100%;
  transition: 0.4s;
}
footer .copyright a {
  text-decoration: none;
  color: rgb(255, 255, 255);
  text-align: center;
  display: block;
  width: 100%;
  padding: 1px 0px 10px 55px;
  transition: 0.4s;
}

/* ---- Adding media queries to the footer section ---- */
@media (max-width: 768px) {
  footer {
    background: #0096fa;
  }

  footer a,
  footer .social-media-iconsli a {
    color: #000000;
  }
  footer .copyright a {
    display: block;
    margin: auto;
    text-align: center;
    width: 100%;
    padding: 1px 0px 10px 0px;
  }
  footer .copyright a:hover {
    color: #ff0000;
    transform: scale(1.1);
  }
  footer .social-media-icons {
    margin: auto;
    text-align: center;
    margin: auto;
    width: 100%;
    padding: 10px 0px;
  }
}
