.home {
  background: #000000;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.home .title {
  width: 20%;
  margin-top: 10%;
  margin-bottom: 10%;
  margin-right: 5%;
  font-size: 1em;
  text-align: center;
  padding: 0.5rem;
}

.home .title h1 {
  font-size: 2rem;
  color: #00ebf3;
}

.portrait {
  width: 100%;
  display: flex;
  justify-content: center;
}

.home .portrait img {
  height: 100vh;
  width: 100%;
}

@media (max-width: 768px) {
  .home {
    background: #000000;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .portrait {
    margin: 0;
    padding: 0%;
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .home .title {
    margin-top: 5px;
    flex-direction: row;
    margin: 1px;
    padding: 1px;
    width: 100%;
    color: #c5cffd;
  }

  .home .title h1 {
    font-size: 2rem;
  }

  .home .portrait img {
    height: 70%;
    width: 100%;
  }
}

button p{
  padding: 10px; 
  padding-top: 20px;
}
.project {
  flex-direction: column;
  margin-bottom: 200px;
  margin-top: 120px;
}
.card-body a {
  margin: 30px;
}

@media (max-width: 768px) {
  .project {
    margin-top: 100px;
  }
}

.projects {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: scroll;
  margin-bottom: 200px;
}
.projects .projectTitle {
  text-align: center;
  color: #3e497a;
  font-size: 60px;
  font-weight: bolder;
}

.Lists {
  width: 70vw;
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
  margin-bottom: 20%;
}

/* MENU ITEM STYLING */

.ProjectTopic {
  border-radius: 15px;
  width: 300px;
  height: 300px;
  margin: 40px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  text-align: center;
  transition: 0.7s;
}
.ProjectTopic:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  transition: 0.6s;
  -webkit-transform: scale(1.6);
          transform: scale(1.6);
}

.ProjectTopic .bgImage {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.ProjectTopic h1 {
  font-size: 25px;
}

.card-body a {
  margin: 10px;
}

.project button svg {
  font-size: 60px;
  color: #ffffff;
}

@media only screen and (max-width: 1300px) {
  .Lists {
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (max-width: 800px) {
  .Lists {
    grid-template-columns: 1fr;
  }
}

@media only screen and (max-width: 800px) {
  .Lists {
    width: 100%;
  }
  .projectItem {
    width: 300px;
    height: 300px;
  }
  button {
    height: 90px;
    width: 90px;
    outline: none;
    border-radius: 50%;
    font-size: 15px;
  }
}

.experience {
  margin: 30px 2px 300px 2px;
  color: #000000;
}

.contact {
  width: 400px;
}

form {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  margin-left: 50px;
  width: 40%;
  height: 40%;
  font-size: 16px;
}

input {
  width: 100%;
  height: 35px;
  padding: 7px;
  outline: none;
  border-radius: 5px;
  border: 1px solid rgb(220, 220, 220);
  box-shadow: -1px 0px 5px rgb(255, 255, 255),
    inset 0px -0px 20px rgb(-15, -15, -15);
}
:focus {
  border: 2px solid rgba(0, 206, 158, 1);
}

textarea {
  max-width: 100%;
  min-width: 100%;
  width: 100%;
  min-height: 120px;
  padding: 7px;
  outline: none;
  border-radius: 5px;
  border: 1px solid rgb(220, 220, 220);
  box-shadow: -1px 0px 5px rgb(255, 255, 255),
    inset 0px -0px 20px rgb(-15, -15, -15);
}
:focus {
  border: 2px solid rgba(0, 206, 158, 1);
  height: 40%;
}

label {
  margin-top: 1rem;
}

input[type="submit"] {
  margin-top: 2rem;
  cursor: pointer;
  background: #1372ee;
  color: white;
  width: 150px;
  box-shadow: -1px 0px 6px rgb(255, 255, 255),
    inset 0px -0px 20px rgb(-15, -15, -15);
}

input[type="submit"]:hover {
  background-color: #074f86;
}

@media (max-width: 768px) {
  form {
    display: flex;
    flex-direction: column;
    margin: 5%;
    width: 90%;
    font-size: 16px;
    margin-bottom: 100%;
  }

  input[type="submit"] {
    width: 100%;
  }
}

footer {
  width: 100%;
  position: fixed;
  bottom: 0;
  display: block;
  justify-content: center;
  align-items: center;
  border-top: 4px solid rgb(255, 255, 255);
  background: rgb(9, 86, 88);
}

footer .social-media-icons {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

footer .social-media-icons li,
footer a {
  list-style: none;
}
footer .social-media-icons li a {
  font-size: 2em;
  color: white;
  margin: 0 10px;
  display: inline-block;
  transition: 0.5s;
}

footer .social-media-icons li a:hover,
footer a:hover {
  color: #ff0000;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

footer a {
  text-decoration: none;
  color: white;
  text-align: center;
  display: block;
  width: 100%;
  transition: 0.4s;
}
footer .copyright a {
  text-decoration: none;
  color: rgb(255, 255, 255);
  text-align: center;
  display: block;
  width: 100%;
  padding: 1px 0px 10px 55px;
  transition: 0.4s;
}

/* ---- Adding media queries to the footer section ---- */
@media (max-width: 768px) {
  footer {
    background: #0096fa;
  }

  footer a,
  footer .social-media-iconsli a {
    color: #000000;
  }
  footer .copyright a {
    display: block;
    margin: auto;
    text-align: center;
    width: 100%;
    padding: 1px 0px 10px 0px;
  }
  footer .copyright a:hover {
    color: #ff0000;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  footer .social-media-icons {
    margin: auto;
    text-align: center;
    margin: auto;
    width: 100%;
    padding: 10px 0px;
  }
}

.navbar {
  width: 100%;
  height: 100px;
  background: rgb(9, 86, 88);
  border-bottom: 4px solid rgb(255, 255, 255);
}

.links {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.links a {
  transition: 0.5s;
}

.links a:hover {
  color: #33ff00;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: 0.5s;
}
.navbar a {
  color: white;
  text-decoration: none;
  margin: 30px;
  font-size: 25px;
}

.toggleButton {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 120px;
}

.toggleButton svg {
  font-size: 50px;
}
.toggleButton button {
  width: 80px;
  height: 80px;
  margin-right: 20px;
  background-color: transparent;
  color: white;
  cursor: pointer;
}

#open {
  height: 100vh;
}

#open .links {
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 100px;
}

#open a {
  width: 100%;
  text-align: center;
}

@media only screen and (max-width: 900px) {
  .navbar a {
    width: 100px;
  }
}

@media only screen and (max-width: 600px) {
  .toggleButton {
    display: flex;
  }
  #close a {
    display: none;
  }
  .navbar a {
    margin: 3px;
  }
  .links {
    padding-top: 0px;
  }
  .links {
    padding-top: 0px;
    height: 10px;
  }
}

@media only screen and (min-width: 600px) {
  .toggleButton {
    display: none;
  }

  .hiddenLinks {
    display: none;
  }

  #open {
    height: 100px;
  }

  #open .links {
    flex-direction: row;
  }
}
@media only screen and (max-width: 600px) {
  #open .links {
    margin-top: 1px;
    margin-bottom: 1px;
    height: 40px;
  }

  #open {
    height: 45vh;
  }
}

.project {
  width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.project h1 {
  margin-top: 50px;
  color: #3e497a;
  font-size: 35px;
}
.project img {
  width: 700px;
  border-radius: 10px;
}

.project p {
  font-size: 40px;
  color: #3e497a;
}

.project svg {
  font-size: 60px;
  color: #3e497a;
}
@media only screen and (max-width: 800px) {
  .project h1 {
    margin-top: -111px;
    color: #ffffff;
    font-size: 25px;
  }

  .project img {
    width: 340px;
    border-radius: 10px;
  }

  .project p {
    font-size: 20px;
    color: #ffffff;
  }
}

.App {
  text-align: center;
}

* {
  box-sizing: border-box;
}

main {
  margin: 0 auto;
  max-width: 98%;
  padding: 20px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

:root {
  --primary: #503f8b;
  --secondary: #4892c4;
  --tertiary: #063664;
  --light: #fafafa;
  --dark: #1a1a1a;
  --black: #000000;
  --error: rgb(99, 0, 0);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

html,
body {
  min-height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background: #000000;
  background: var(--black);
  color: #fafafa;
  color: var(--light);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  margin: 5px 0;
  line-height: 1.5;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

.my-5 {
  margin-top: calc(2.5rem);
  margin-top: var(--spacing-five);
  margin-bottom: calc(2.5rem);
  margin-bottom: var(--spacing-five);
}

button {
  height: 150px;
  width: 150px;
  background-color: #0089fa;
  border: none;
  outline: none;
  border-radius: 100px;
  color: #fff;
  cursor: pointer;
  font-size: 18px;
  font-weight: 200;
  margin-bottom: 150px;
  padding: 12px 15px;
  transition: -webkit-transform 0.6s ease-out;
  transition: transform 0.6s ease-out;
  transition: transform 0.6s ease-out, -webkit-transform 0.6s ease-out;
  display: inline-block;
  color: #ffffff;
  box-shadow: 0px -3px 30px rgb(255, 255, 255),
    inset 0px -8px 20px rgb(-15, -15, -15);
  border: 1px solid rgb(255, 255, 255);
}

button:hover {
  background-color: #00cf11;
  color: #f30000;
  transition: 0.4s;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media only screen and (max-width: 900px) {
  button {
    margin-bottom: 150px;
    padding: 1px 1px;
  }
}

